<template>
  <header>
    <div @click="PageHome()">Home</div>
    <div @click="PageWork()">Work</div>
    <div @click="PageAbout()">About</div>
  </header>
</template>
<script>
export default {
  name: 'Header',
  props: {},
  data() {
    return {
      nowTab: '',
    };
  },
  mounted() {
    this.nowTab = this.GetRouteName();
  },
  computed: {},
  methods: {
    GetRouteName() {
      return this.$router.currentRoute.name;
    },
    PageHome() {
      if (this.nowTab != 'Home') {
        this.$router.push({ path: '/page/Home' });
        this.nowTab = 'Home';
      }
    },
    PageWork() {
      if (this.nowTab != 'Work') {
        this.$router.push({ path: '/page/Work' });
        this.nowTab = 'Work';
      }
    },
    PageAbout() {
      if (this.nowTab != 'About') {
        this.$router.push({ path: '/page/About' });
        this.nowTab = 'About';
      }
    },
  },
  components: {},
};
</script>

